<template>
  <div class="m-tip">
    <div class="popo">
      <i class="jiaoup" v-if="dir.up"></i>
      <i class="jiaobottom" v-if="dir.bottom"></i>
      <i class="jiaoleft" v-if="dir.left"></i>
      <p class="title">{{ tip }}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: ["tip", "dir"]
};
</script>

<style lang="scss">
.m-tip {
  position: absolute;

  .popo {
    height: 30px;
    width: 200px;
    box-shadow: 0px 0px 5px 0px #ccc;
    background: rgba(248, 71, 71, 0.7);
    border-radius: 5px;

    .title {
      line-height: 30px;
      box-sizing: border-box;
      padding: 0 10px;
      color: #fff;
    }

    .jiaoup {
      width: 0;
      height: 0;
      border: 5px solid #000;
      border-color: transparent;
      border-bottom-color: rgba(248, 71, 71, 0.2);
      display: block;
      position: absolute;
      left: 23px;
      top: -9px;
    }

    .jiaobottom {
      width: 0;
      height: 0;
      border: 5px solid #000;
      border-color: transparent;
      border-top-color: rgba(248, 71, 71, 0.2);
      display: block;
      position: absolute;
      left: 23px;
      top: 30px;
    }
    .jiaoleft {
      width: 0;
      height: 0;
      border: 5px solid #000;
      border-color: transparent;
      border-right-color: rgba(248, 71, 71, 0.2);
      display: block;
      position: absolute;
      left: -10px;
      top: 11px;
    }
  }
}
</style>


