<template>
  <div class="m-istyle">
    <dl @mouseover="over" class="house">
      <dt>推荐民宿</dt>
      <dd :class="{active: kind == 'xia'}" data-type="xia">厦门</dd>
      <dd :class="{active: kind == 'quan'}" data-type="quan">泉州</dd>
      <dd :class="{active: kind == 'fu'}" data-type="fu">福州</dd>
      <dd :class="{active: kind == 'bei'}" data-type="bei">北京</dd>
      <dd :class="{active: kind == 'shang'}" data-type="shang">上海</dd>
      <dd :class="{active: kind == 'hang'}" data-type="hang">杭州</dd>
      <dd :class="{active: kind == 'su'}" data-type="su">苏州</dd>
    </dl>
    <div>
      <ul class="ibody">
        <li v-for="(item, index) in list" :key="index">
          <el-card :body-style="{ padding: '0px' }" shadow="never">
            <img :src="item.img" class="image" />
            <div class="cbody">
              <div class="title" :title="item.title">{{ item.title }}</div>
              <div class="sub-title" :title="item.subtitle">{{ item.subtitle }}</div>
              <div class="price-info">
                <span class="current-price-wrapper">
                  <span class="price-symbol numfont">¥</span>
                  <span class="current-price numfont">{{ item.price.current_price }}</span>
                </span>
                <span class="old-price">原价¥{{ item.price.old_price }}</span>
                <span class="sold bottom-right-info">{{ item.add }}</span>
              </div>
            </div>
          </el-card>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      kind: "xia",
      list: [
        {
          img:
            "https://img.meituan.net/iphoenix/f495bd691316de83bd136b49171b1fcb531285.png@740w_416h_1e_1c",
          title: "COSTA COFFEE（新国际酒店）",
          subtitle: "巧克力苏卷",
          price: {
            current_price: 18,
            old_price: 36
          },
          add: "厦门"
        },
        {
          img:
            "https://img.meituan.net/iphoenix/f495bd691316de83bd136b49171b1fcb531285.png@740w_416h_1e_1c",
          title: "COSTA COFFEE（新国际酒店）",
          subtitle: "巧克力苏卷",
          price: {
            current_price: 18,
            old_price: 36
          },
          add: "厦门"
        }
      ]
    };
  },
  methods: {
    over(e) {
      let dom = e.target;
      let i = dom.getAttribute("data-type");
      let tag = dom.tagName.toLowerCase();
      if (tag != "dd") {
        return false;
      }
      this.kind = i;
    }
    //axios
  }
};
</script>

<style lang="scss">
@import "@/assets/css/index/artistic.scss";
</style>


