<template>
  <div class="m-menu">
    <!-- 导航菜单 -->
    <dl class="nav" @mouseleave="mouseleave">
      <dt>全部分类</dt>
      <dd v-for="(item, index) in menuList" :key="index" @mouseenter="mouseenter(item)">
        <i :class="item.type"></i>
        {{ item.name }}
        <span class="arrow"></span>
      </dd>
    </dl>
    <div class="detail" v-if="showDetail" @mouseenter="detail" @mouseleave="mouseleave">
      <template v-for="(item, index) in showDetail.items">
        <h2 :key="index">
          {{ item.title }}
          <!-- <span>更多</span> -->
        </h2>
        <span v-for="(p, i) in item.items" :key=" p + '_' + i">{{ p }}</span>
      </template>
    </div>
  </div>
</template>

<script>
import api from "@/api/index.js";
export default {
  data() {
    return {
      showDetail: null,
      timer: null,
      menuList: []
    };
  },
  methods: {
    mouseenter(item) {
      this.showDetail = item;
    },
    mouseleave() {
      let self = this;
      this.timer = setTimeout(() => {
        this.showDetail = null;
      }, 200);
    },
    detail() {
      clearTimeout(this.timer);
    }
  },
  created() {
    api.getMenuList().then(res => {
      // console.log(res.data.data);
      this.menuList = res.data.data;
    });
  }
};
</script>

<style lang="scss">
</style>


