<template>
  <div class="m-geo">
    <div class="position">
      <i class="el-icon-location" />
      {{ $store.state.position || '福建'}}
      <router-link class="changeCity" :to="{ name: 'changeCity' }">切换城市</router-link>[
      <a href="/changeCity">集美区</a>
      <a href="/changeCity">银江路</a>
      <a href="/changeCity">月明楼</a> ]
    </div>
    <div class="m-user" v-if="!$store.state.userName">
      <router-link class="login" :to="{name: 'login'}">立即登录</router-link>
      <router-link class="register" :to="{name: 'register'}">注册</router-link>
    </div>
    <div class="m-user" v-if="$store.state.userName">
      <router-link class="login" :to="{name: 'index'}">Hi,{{$store.state.userName}}</router-link>
      <router-link class="login" :to="{name: 'login'}">注销</router-link>
    </div>
    <tip class="posi" :tip="change" :dir="dir" />
  </div>
</template>

<script>
import api from "@/api/index.js";
import tip from "@/components/tip.vue";
export default {
  data() {
    return {
      change: "点击这里切换城市哟!!!",
      dir: {
        up: true
      }
    };
  },
  // created() {
  //   api.getLocation().then(res => {
  //     this.$store.dispatch("setPosition", res.data.data);
  //     console.log(res.data.data);
  //   });
  // },
  components: {
    tip
  }
};
</script>

<style lang="scss">
@import "@/assets/css/public/header/topbar.scss";
.posi {
  left: 30px;
}
</style>



