<template>
  <div class="search-panel">
    <el-row class="m-header-searchbar">
      <el-col :span="3" class="left">
        <router-link to="/index">
          <img src="//s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png" alt="美团" />
        </router-link>
      </el-col>
      <el-col :span="15" class="center">
        <div class="wrapper">
          <el-input
            v-model="searchWord"
            placeholder="搜索商家或地点"
            @focus="focus"
            @blur="blur"
            @input="input"
          ></el-input>
          <tip class="searchTip" :tip="change" :dir="dir" />
          <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
          <dl class="hotPlace" v-if="isHotPlace">
            <dt>热门搜索</dt>
            <dd v-for="(item, index) in hotList" :key="index">
              <router-link :to="{name:'goods', params: {name: item}}">{{ item }}</router-link>
            </dd>
          </dl>
          <dl class="searchList" v-if="isSearchList">
            <router-link
              :to="{name:'goods', params: {name: item}}"
              v-for="(item, index) in searchList"
              :key="index"
            >
              <dd>{{item}}</dd>
            </router-link>
          </dl>
        </div>
        <p class="suggest">
          <router-link
            :to="{name:'goods', params: {name: item}}"
            v-for="(item, index) in suggest"
            :key="index"
          >{{item}}</router-link>
        </p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import api from "@/api/index.js";
import tip from "@/components/tip.vue";

// console.log(axios);
export default {
  components: {
    tip
  },
  data() {
    return {
      searchWord: "",
      isFocus: false,
      hotList: [],
      searchList: [],
      suggest: [],
      change: "点这里搜索'火锅' 等字样",
      dir: {
        bottom: true
      }
    };
  },
  methods: {
    focus() {
      this.isFocus = true;
    },
    blur() {
      let self = this;
      setTimeout(() => {
        self.isFocus = false;
      }, 100);
    },
    input() {
      api.getSearchHotWord().then(res => {
        this.searchList = res.data.data.list;
      });
    },
    search() {
      this.$router.push({ path: "msg/" + this.searchWord });
    }
  },
  computed: {
    isHotPlace() {
      return this.isFocus && !this.searchWord;
    },
    isSearchList() {
      return this.isFocus && this.searchWord;
    }
  },
  created() {
    api.getSug().then(res => {
      this.hotList = res.data.data;
      this.suggest = res.data.data;
    });
  }
};
</script>

<style lang="scss">
.searchTip {
  top: -36px;
  left: 260px;
}
</style>



