<template>
  <div class="page-index">
    <el-row>
      <el-col :span="5">
        <m-menu />
      </el-col>
      <el-col :span="19">
        <left />
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <contain :nav="iStyle" />
      </el-col>
      <el-col :span="24">
        <contain-movie />
      </el-col>
    </el-row>
  </div>
</template>

<script>
import MMenu from "@/components/index/menu.vue";
import left from "@/components/index/left.vue";
import contain from "@/components/index/contain.vue";
import containMovie from "@/components/index/containMovie.vue";
import containHouse from "@/components/index/containHouse.vue";
export default {
  data() {
    return {
      iStyle: {
        title: "有格调",
        list: [
          {
            text: "全部",
            tab: "all"
          },
          {
            text: "约会聚餐",
            tab: "food"
          },
          {
            text: "丽人SPA",
            tab: "spa"
          },
          {
            text: "电影演出",
            tab: "movie"
          },
          {
            text: "品质出游",
            tab: "travel"
          }
        ]
      }
    };
  },
  components: {
    MMenu,
    left,
    contain,
    containMovie,
    containHouse
  }
};
</script>

<style lang="scss">
@import "@/assets/css/index/index.scss";
</style>


