<template>
  <div class="m-products-list">
    <ul>
      <li v-for="(item) in nav" :key="item.key" :class="{'s-nav-active': item.active}">
        {{ item.name }}
        <i v-if="item.icon" class="el-icon-d-caret"></i>
      </li>
    </ul>
    <el-row>
      <item v-for="(item,index) in productList" :key="index" :meta="item" />
    </el-row>
  </div>
</template>

<script>
import api from "@/api/index.js";
import item from "./item";
export default {
  components: {
    item
  },
  data() {
    return {
      nav: [
        {
          key: "s-default",
          name: "智能排序",
          active: true,
          icon: false
        },
        {
          key: "s-price",
          name: "加格最低",
          active: false,
          icon: true
        },
        { key: "s-sale", name: "人气最高" },
        {
          key: "s-comment",
          name: "评价最高",
          active: false,
          icon: false
        }
      ],
      productList: []
    };
  },
  created() {
    api.getGoodList().then(res => {
      // console.log(res.data.data);
      this.productList = res.data.data;
    });
  }
};
</script>

<style lang="scss">
@import "@/assets/css/products/index.scss";
</style>


