<template>
  <dl class="s-item">
    <dt>
      <img :src="imageList[Math.floor((Math.random() * 10))]" alt="图片" />
    </dt>
    <dd>
      <h3>
        <router-link :to="{name: 'productDeatil'}">{{meta.title}}</router-link>
      </h3>
      <p>
        <el-rate
          v-model="meta.score"
          disabled
          show-score
          text-color="#ff9900"
          score-template="{value}"
        ></el-rate>
        <span v-if="meta.score > 4.5">很好</span>
        <span v-else-if="meta.score > 4">好</span>
        <span v-else-if="meta.score > 3.5">不错</span>
        <span v-else-if="meta.score > 3">一般</span>
        <span v-else>很差</span>
        <span class>{{meta.score}}分</span>
        <!-- <span class="s-item-comment-total">{{meta.commentNum}}人评论</span> -->
      </p>
      <p>
        <span class="s-item-tab">
          <span
            v-for="(v, i) in meta.detailUrl"
            :key="v"
          >{{v + (i < meta.detailUrl.length - 1 ? '|' : '')}}</span>
        </span>
        <span class="s-item-addr">{{meta.areaName}}</span>
        <span>
          <a href="#">
            <i class="el-icon-location"></i>
            查看地图
          </a>
        </span>
      </p>
      <p>
        <span class="s-item-price">人均¥{{meta.avgPrice}}</span>
      </p>
      <ul class="deal-items">
        <li class="items" v-for="(item, index) in detail.slice(0, 2)" :key="index">
          <p class="deal-title">{{item.title}}</p>
          <p>
            <span class="deal-price">¥{{item.price}}</span>
            <span class="deal-old-price">门市价¥{{item.conterPrice}}</span>
            <span class="deal-sales">已售{{item.saleNum}}</span>
          </p>
        </li>
      </ul>
    </dd>
  </dl>
</template>
<script>
export default {
  props: ["meta"],
  data() {
    return {
      detail: [
        {
          title: "火鸡宴，建议10-14人食用",
          price: 909,
          conterPrice: 1150,
          saleNum: 0
        },
        {
          title: "100元代金卷1张",
          price: 28.9,
          contterPrice: 100,
          saleNum: 2305
        }
      ],
      imageList: [
        "//p0.meituan.net/deal/112d89d54b52783f5ac73bfc51a9fece39142.jpg@275w_156h_1e_1c",
        "//p0.meituan.net/waimaipoi/c187413f824abddf1f1032d2f503960917644.jpg@275w_156h_1e_1c",
        "//p0.meituan.net/dealwatera/cf7cd9e175266fe738acd36381672b00105226.jpg@275w_156h_1e_1c",
        "//p0.meituan.net/deal/22e7b4d5fdbd2ba3af5098288eb4971950892.jpg",
        "//p0.meituan.net/deal/f694af4d5f1aaaa570177d75b105a617182534.jpg",
        "http://qcloud.dpfile.com/pc/grNsNYZlNaPogdO5JAMS1eGBn8bbrUJGcVJrjKlUZMkXXoChnDfITy56gPxPCx0hZSUjBikR5Ecy-DoGYkMhlg.jpg",
        "//p1.meituan.net/xianfu/d6f4577800c9622a206bd0c25388616040960.jpg@275w_156h_1e_1c",
        "//p1.meituan.net/msmerchant/1669d4c9222397c3172400d6be2a322b369040.jpg@275w_156h_1e_1c",
        "//p1.meituan.net/waimaipoi/856435fc730df39ab67bc317c0add69f43008.jpg@275w_156h_1e_1c",
        "http://qcloud.dpfile.com/pc/3uh89jA0lt5-IWiTXiUyBB5kjSslKdBnaCubqpLbgs2c_KUldn9IAxRtkEd5MYR0ZSUjBikR5Ecy-DoGYkMhlg.jpg"
      ]
    };
  },
  created() {
    // console.log(this.meta);
  }
};
</script>


