<template>
  <div class="m-istyle">
    <dl class="like">
      <dt>猜你喜欢</dt>
      <dd class="active">为你甄选最适合的</dd>
    </dl>
    <div>
      <ul class="ibody">
        <li v-for="(item, index) in list" :key="index" class="like">
          <el-card :body-style="{ padding: '0px' }" shadow="never" class="l">
            <img :src="item.img" class="image" />
            <div class="cbody">
              <div class="title" :title="item.title">{{ item.title }}</div>

              <div class="score">
                <ul class="back clearfix">
                  <li>
                    <i class="el-icon-star-on"></i>
                  </li>
                  <li>
                    <i class="el-icon-star-on"></i>
                  </li>
                  <li>
                    <i class="el-icon-star-on"></i>
                  </li>
                  <li>
                    <i class="el-icon-star-on"></i>
                  </li>
                  <li>
                    <i class="el-icon-star-on"></i>
                  </li>
                </ul>
                <ul class="dis clearfix">
                  <li>
                    <i class="el-icon-star-on start"></i>
                  </li>
                  <li>
                    <i class="el-icon-star-on start"></i>
                  </li>
                  <li>
                    <i class="el-icon-star-on start"></i>
                  </li>
                  <li>
                    <i class="el-icon-star-on start"></i>
                  </li>
                  <li>
                    <i class="el-icon-star-on"></i>
                  </li>
                </ul>
                <div class="comment-number">456个评价</div>
              </div>
              <div class="sub-add" :title="item.subtitle">{{ item.subtitle }}</div>
              <div class="price-info">
                <span class="current-price-wrapper">
                  <span class="price-symbol big">¥</span>
                  <span class="current-price siki">{{ item.price }}</span>
                  <span class="price-symbol sm">起</span>
                </span>
              </div>
            </div>
          </el-card>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      kind: "all",
      list: [
        {
          img:
            "//p1.meituan.net/deal/635e42f381e3adc184a4ba679777f51b261660.jpg@267w_150h_1e_1c",
          title: "意大利西餐厅",
          subtitle: "巧克力苏卷",
          price: 85.8,
          add: "大润发"
        },
        {
          img:
            "//p1.meituan.net/msmerchant/ab75f9fff2844327fc6b33f424c0998939894.jpg@267w_150h_1e_1c",
          title: "泥锅麻辣串",
          subtitle: "大象",
          price: 18,
          add: "大象"
        }
      ]
    };
  },
  methods: {
    over(e) {
      let dom = e.target;
      let i = dom.getAttribute("data-type");
      let tag = dom.tagName.toLowerCase();
      if (tag != "dd") {
        return false;
      }
      this.kind = i;
    }
    //axios
  }
};
</script>

<style lang="scss">
@import "@/assets/css/index/artistic.scss";
</style>


