<template>
  <div class="m-istyle">
    <dl @mouseover="over">
      <dt>{{ nav.title }}</dt>
      <dd
        v-for="(item, index) in nav.list"
        :key="index"
        :class="{active: kind == item.tab}"
        :data-type="item.tab"
      >{{ item.text }}</dd>
    </dl>
    <div>
      <ul class="ibody">
        <li v-for="(item, index) in resultData[kind]" :key="index">
          <el-card :body-style="{ padding: '0px' }" shadow="never">
            <img :src="item.image" class="image" />
            <div class="cbody">
              <div class="title" :title="item.title">{{ item.title }}</div>
              <div class="sub-title" :title="item.subtitle">{{ item.subtitle }}</div>
              <div class="price-info" v-if="item.price">
                <span class="current-price-wrapper">
                  <span class="price-symbol numfont">¥</span>
                  <span class="current-price numfont">{{ item.price }}</span>
                </span>
                <span class="old-price">原价¥{{ item.price }}</span>
                <span class="sold bottom-right-info">{{ item.add }}</span>
              </div>
              <!-- <div class="price-info" v-else-if="!item.rentNum">
                <span class="current-price-wrapper">
                  <span class="price-symbol numfont">¥</span>
                  <span class="current-price numfont">抢光了</span>
                </span>
              </div>
              <div class="price-info" v-else>
                <span class="current-price-wrapper">
                  <span class="price-symbol numfont">¥</span>
                  <span class="current-price numfont">
                    {{ item.price.avg_price }}
                    <span class="units">/ {{ item.price.units }}</span>
                  </span>
                </span>
              </div>-->
            </div>
          </el-card>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import api from "@/api/index.js";
export default {
  data() {
    return {
      kind: "all",
      resultData: {},
      list: [
        {
          img:
            "https://img.meituan.net/iphoenix/f495bd691316de83bd136b49171b1fcb531285.png@740w_416h_1e_1c",
          title: "COSTA COFFEE（新国际酒店）",
          subtitle: "巧克力苏卷",
          price: {
            current_price: 18,
            old_price: 36
          },
          rentNum: 0,
          add: "厦门"
        },
        {
          img:
            "https://img.meituan.net/iphoenix/f495bd691316de83bd136b49171b1fcb531285.png@740w_416h_1e_1c",
          title: "COSTA COFFEE（新国际酒店）",
          subtitle: "巧克力苏卷",
          price: {
            current_price: 18,
            old_price: 36
          },
          rentNum: 1,
          add: "厦门"
        },
        {
          img:
            "https://img.meituan.net/iphoenix/f495bd691316de83bd136b49171b1fcb531285.png@740w_416h_1e_1c",
          title: "COSTA COFFEE（新国际酒店）",
          subtitle: "巧克力苏卷",
          price: {
            current_price: 18,
            old_price: 36,
            avg_price: 18,
            units: "人均"
          },
          rentNum: 1,
          add: "厦门"
        }
      ]
    };
  },
  props: ["nav"],
  methods: {
    over(e) {
      let dom = e.target;
      let i = dom.getAttribute("data-type");
      let tag = dom.tagName.toLowerCase();
      if (tag != "dd") {
        return false;
      }
      this.kind = i;
    }
    //axios
  },
  created() {
    api.getIndexContain().then(res => {
      // console.log(res.data.data);
      this.resultData = res.data.data;
    });
  }
};
</script>

<style lang="scss">
@import "@/assets/css/index/artistic.scss";
</style>


