<template>
  <div class="m-hcity">
    <dl>
      <dt>{{ title }}</dt>
      <router-link to="/index" v-for="(item, index) in list" :key="index">
        <dd @click="change(item)">{{ item }}</dd>
      </router-link>
    </dl>
  </div>
</template>

<script>
import api from "@/api/index.js";

export default {
  props: ["title", "list"],
  methods: {
    change(val) {
      this.$store.dispatch("setPosition", val);
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/css/changecity/hot.scss";
</style>


