import axios from 'axios'
axios.defaults.baseURL = 'http://open.duyiedu.com';
axios.interceptors.request.use(function (config) {
  config.params = {
    ...config.params,
    appkey: '15859996325_1586163200606',

  }
  return config;
}, function (error) {
  return Promise.reject(error);
})

export default axios;



