<template>
  <div :class="{'choose-wrap': true, 'disable': disabled}" @click="click" v-document-click="dc">
    <div class="choose">
      <span :value="value">{{ value }}</span>
      <i class="el-icon-caret-bottom"></i>
      <div class="mt-content" v-if="isShow">
        <h2>{{ title }}</h2>
        <div :class="['wrapper', width]">
          <div class="col" v-for="(ld, index) in renderList" :key="index">
            <router-link :to="isIndex" class="i" @click="choice(item)">
              <span
                :class="{'mt-item': true, 'active': item == value}"
                v-for="(item,index) in ld"
                :key="index"
                @click="choice(item)"
              >{{ item }}</span>
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: ["list", "title", "value", "isShow", "disabled", "width", "isIndex"],
  methods: {
    click(e) {
      e.stopPropagation();

      if (!this.disabled) {
        this.$emit("show", true);
      } else {
        alert("请先选择省份");
      }
    },
    dc(e) {
      this.$emit("show", false);
    },
    choice(item) {
      this.$emit("change", item);
    }
  },
  computed: {
    renderList: function() {
      let col = Math.ceil(this.list.length / 12);
      let resultList = [];
      for (let i = 0; i < col; i++) {
        let data = this.list.slice(i * 12, i * 12 + 12);
        resultList.push(data);
      }
      return resultList;
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/css/changecity/select.scss";
</style>


