<template>
  <div class="page-product">
    <el-row class="m-crumbs">
      <mb />
    </el-row>
    <el-row>
      <el-col :span="19">
        <el-row>
          <categroy />
        </el-row>
        <el-row>
          <list />
        </el-row>
      </el-col>
      <el-col :span="5">
        <img src="@/assets/img/map.png" alt class="mapImg" />
        <like />
      </el-col>
    </el-row>
  </div>
</template>

<script>
import mb from "@/components/product/mb.vue";
import categroy from "@/components/product/categroy.vue";
import list from "@/components/product/list.vue";
import like from "@/components/product/youlike.vue";

export default {
  components: {
    mb,
    categroy,
    list,
    like
  }
};
</script>

<style>
.mapImg {
  width: 237px;
  height: 237px;
  margin-left: 9px;
  border-radius: 2%;
}
</style>


