<template>
  <div class="m-header">
    <el-row>
      <top-bar></top-bar>
    </el-row>
    <el-row class="header-shadow">
      <search-bar></search-bar>
    </el-row>
  </div>
</template>

<script>
import topBar from "./topbar";
import searchBar from "./searchbar";
export default {
  components: {
    topBar,
    searchBar
  }
};
</script>

<style lang="scss">
@import "@/assets/css/public/header/search.scss";
@import "@/assets/css/public/header/index.scss";
</style>



