<template>
  <div>
    <span class="name">按省份选择</span>
    <choose
      :list="proList"
      title="省份"
      :value="province"
      :isShow="provinceShow"
      @show="cchange"
      @change="cp"
      width="pro"
      isIndex
    />
    <choose
      :list="cityList"
      title="城市"
      :value="city"
      :isShow="cityShow"
      @show="pchange"
      @change="cc"
      :disabled="cityDis"
      width="city"
      isIndex="/index"
    />

    <span>直接搜索 :</span>
    <el-select
      v-model="searchword"
      filterable
      remote
      reserve-keyword
      placeholder="请输入关键词"
      :remote-method="remoteMethod"
      :loading="loading"
    >
      <el-option v-for="item in searchList" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </div>
</template>

<script>
import choose from "@/components/change/choose.vue";
import api from "@/api/index.js";
export default {
  components: {
    choose
  },
  data() {
    return {
      proList: [],
      province: "省份",
      cityList: [],
      city: "城市",
      cityShow: false,
      provinceShow: false,
      searchList: [],
      searchword: "",
      loading: false,
      cityDis: true,
      origin: []
    };
  },
  methods: {
    cchange(flag) {
      this.provinceShow = flag;
      if (flag) {
        this.cityShow = false;
      }
    },
    pchange(flag) {
      this.cityShow = flag;
      if (flag) {
        this.provinceShow = false;
      }
    },
    remoteMethod(val) {
      //请求后端接口
    },
    cp(value) {
      this.province = value;
      this.cityDis = false;
      let res = this.origin[this.proList.indexOf(value)].cityInfoList;
      this.cityList = res.map(item => item.name);
    },
    cc(value) {
      this.city = value;
      this.$store.dispatch("setPosition", value);
    }
  },
  created() {
    api.getCityList().then(res => {
      this.origin = res.data.data;
      this.proList = res.data.data.map(item => item.provinceName);
    });
  }
};
</script>

<style lang="scss">
@import "@/assets/css/changecity/iselect.scss";
</style>


