<template>
  <div class="page-changeCity">
    <el-row>
      <province />
    </el-row>
    <el-row>
      <hot-city title="热门城市 : " :list="hlist" />
    </el-row>
    <el-row>
      <hot-city title="最近访问 : " :list="see"></hot-city>
    </el-row>
    <el-row>
      <cate />
    </el-row>
  </div>
</template>

<script>
import hotCity from "@/components/change/hotCity.vue";
import province from "@/components/change/province.vue";
import cate from "@/components/change/cate.vue";
import api from "@/api/index.js";

export default {
  components: {
    province,
    hotCity,
    cate
  },
  data() {
    return {
      hlist: ["武夷山", "厦门", "福州"],
      see: ["武夷山", "厦门", "福州", "福州"]
    };
  },
  created() {
    api.getHotCity().then(res => {
      this.hlist = res.data.data.map(item => item.name);
    });
    api.getNearCity().then(res => {
      this.see = res.data.data.map(item => item.name);
    });
  }
};
</script>

<style>
</style>


