<template>
  <div class="m-product-categroy">
    <dl class="classic">
      <dt>分类</dt>
      <dt>全部</dt>
      <dd v-for="(item) in menulist" :key="item.type">
        <mselect :name="item.title" :list="item.subList" />
      </dd>
    </dl>
    <dl class="classic">
      <dt>区域</dt>
      <dt>全部</dt>
      <dd v-for="(item) in arealist" :key="item.title">
        <mselect :name="item.title" :list="item.subList" />
      </dd>
    </dl>
  </div>
</template>

<script>
import api from "@/api/index.js";
import mselect from "./select";
export default {
  data() {
    return {
      menulist: [],
      arealist: [
        {
          title: "推荐商圈",
          subList: "望京"
        }
      ]
    };
  },
  components: {
    mselect
  },
  created() {
    api.getAreaList().then(res => {
      console.log(res.data.data);
      this.menulist = res.data.data;
    });
    api.getFenList().then(res => {
      console.log(res.data);
      // this.arealist = res.data;
    });
  }
};
</script>

<style>
</style>


